import React from 'react'
import Header from '../Header';
import Footer from './Footer';
import './OmMig.css';


function OmMig() {
    return (
        <div className="OmMig">
            <Header />
            <section className="MigText">
            <h2>Zeynab Zuwa</h2>
            <p className="Paragraf">Jag r entusiastisk och blir snabbt engagerad i det jag gr. 
                Personlig utveckling r vldigt viktigt fr mig fr det finns alltid ngot nytt att lra sig och det finns alltid rum fr frbttringar.
                Knslan av att lyckas r ngot som driver mig och drfr r jag alltid mn om att gra ett utmrkt jobb fr att kunna uppn bsta resultat i allt jag gr.    
                Drfr valde jag att brja studera till systemutvecklare fr att jag gillar det faktum att man kan vara med i hela utvecklingsprocessen.</p>
                </section>
            <Footer/>
            
            
        </div>
    )
}

export default OmMig



